import React, { useState } from "react";
import "../Css/Modal.css"
import { Container, Table, InputGroup, FormControl, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

export default function InsertPole({ onCreate }){

    const [inputs,setInputs] = useState({
        poleNum: '',
        poleDate: '',
        poleAdmin: '',
        poleImg:''
    });

    const { poleNum, poleDate, poleAdmin, poleImg } = inputs;
    const onChange = (e) => {
        const { name, value } = e.target;
        setInputs({
            ...inputs,
            [name]:value
        });
    };

    const onSubmit = (e) =>{
        onCreate(inputs);
    }

    return(
        <>
        <Container fluid='xl' className="mt-4 ">
            <Table striped bordered variant="dark" className="m-auto mt-4 w-75">
                <thead>
                    <tr>
                        <th> </th>
                    </tr>
                </thead>
                <tbody>
                    <tr><td> </td></tr>
                    <tr>
                        <td>
                        <InputGroup className="p-auto" >
                            <FormControl placeholder="  " aria-label="Default" aria-describedby="inputGroup-sizing-default" value={poleNum} name="poleNum" onChange={onChange}/>
                        </InputGroup>
                        </td>
                    </tr>
                    <tr><td> </td></tr>
                    <tr>
                        <td>
                            <InputGroup className="p-auto" >
                                <FormControl aria-label="Default" aria-describedby="inputGroup-sizing-default" placeholder="  " value={poleDate} name="poleDate" onChange={onChange}/>
                            </InputGroup>
                        </td>
                    </tr>
                    <tr><td> </td></tr>
                    <tr>
                        <td>
                            <InputGroup className="p-auto" >
                                <FormControl aria-label="Default" aria-describedby="inputGroup-sizing-default" placeholder="  " value={poleAdmin} name="poleAdmin" onChange={onChange}/>
                            </InputGroup>
                        </td>
                    </tr>
                    <tr><td> </td></tr>
                    <tr>
                        <td>
                            <InputGroup className="p-auto" >
                                <FormControl aria-label="Default" aria-describedby="inputGroup-sizing-default" placeholder="  " value={poleImg} name="poleImg" onChange={onChange}/>
                            </InputGroup>
                        </td>
                    </tr>
                    <tr><td><Button variant="dark" className="w-25" onClick={onSubmit}></Button></td></tr>
                </tbody>
            </Table>
        </Container>
        </>
    )
}
