/* global kakao */
import React, { useEffect, useState } from "react";
import styled, { css } from 'styled-components';
import {MdDone, MdDelete} from 'react-icons/md'
import { Form, Container, Table, Nav } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const TodoItem = React.memo(function TodoItem({ todo, onToggle, onRemove }) {
  return (
    <Nav className="align-items-center p-1">
      <CheckCircle onClick={() => onToggle(todo.id) } done={todo.done}>{todo.done && <MdDone />}</CheckCircle>
      <Text done={todo.done}>{todo.text}</Text>
      <Remove onClick={()=> onRemove(todo.id) }><MdDelete/></Remove>
    </Nav>
  );
});

const TodoList = React.memo(function TodoList({ todos, onToggle, onRemove }) {
  return (
    <>
      {todos.map(todo => (
        <tr key={todo.id}>
          <td>
            <TodoItem todo={todo} onToggle={onToggle} onRemove={onRemove} />
          </td>
        </tr>
      ))}
    </>
  );
});

export default function PoleInfo({ todos, onCreate, onToggle, onRemove, poleInfo }){
    useEffect(()=>{
        var container = document.getElementById('map');
        var options = {
          center: new kakao.maps.LatLng(37.365264512305174, 127.10676860117488),
          level: 3
        };
        var map = new kakao.maps.Map(container, options);
        var markerPosition  = new kakao.maps.LatLng(37.365264512305174, 127.10676860117488); 
        var marker = new kakao.maps.Marker({
          position: markerPosition
      });
      marker.setMap(map);
    }, []);
    
    const { kakao } = window;

    const [text, setText] = useState('');
    const onChange = e => setText(e.target.value);
    const onSubmit = e => {
      e.preventDefault(); // Submit     
      onCreate(text);
      setText(''); //  
      console.log(todos);
    }

    return(
    <>
      <Container fluid='xl' className="mt-4">
        <Table striped bordered hover variant="dark" className="m-auto w-75">
            <thead>
              <tr>
                <th style={thStyle} colSpan={2}> : {poleInfo.poleNum}</th>
              </tr>
            </thead>
          <tbody>
            <tr>
              <td></td>
              <td>{poleInfo.poleAdmin}</td>
            </tr>
            <tr>
              <td> </td>
              <td>{poleInfo.poleTilt[poleInfo.poleTilt.length-1]}</td>
            </tr>
            <tr>
              <td> </td>
              <td>{poleInfo.poleDate[poleInfo.poleDate.length-1]}</td>
            </tr>
          </tbody>
        </Table>
        <Table striped bordered hover variant="dark" className="m-auto mt-4 w-75">
            <thead>
              <tr>
                <th style={thStyle}> </th>
              </tr>
            </thead>
          <tbody>
            <TodoList todos={todos} onToggle={onToggle} onRemove={onRemove}/>
              <tr>
                <td>
                  <Form onSubmit={onSubmit}>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>  </Form.Label>
                      <Form.Control type="text" autoFocus placeholder="   , Enter  " value={text} onChange={onChange}/>
                    </Form.Group>
                  </Form>
                </td>
              </tr>
          </tbody>
        </Table>
        <Table striped bordered hover variant="dark" className="m-auto mt-4 w-75">
          <thead>
            <tr>
              <th style={thStyle}> </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th id="map" style={{width:"60%", height:"30vh"}}></th>
            </tr>
          </tbody>
        </Table>
      </Container>  
    </>
    )
}

const thStyle={
  fontSize:"24px"
}

const Remove = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  color: #dee2e6;
  font-size: 24px;
  cursor: pointer;
  &:hover {
    color: #ff6b6b;
  }
`;

const CheckCircle = styled.div`
  width: 28px;
  height: 28px;
  border-radius: 13px;
  border: 1px solid #ced4da;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  cursor: pointer;
  ${props =>
    props.done &&
    css`
      border: 1px solid #38d9a9;
      color: #38d9a9;
    `}
`;

const Text = styled.div`
  font-size: 21px;
  color: #495057;
  ${props =>
    props.done &&
    css`
      color: #ced4da;
    `}
`;