const ADD_POLE = "info/ADD_POLE";

export const addPole = text => ({
    type: ADD_POLE,
    inputs:{
        id: text.id,
        poleNum: text.poleNum,
        poleDate: [text.poleDate,],
        poleTilt: [90,],
        poleAdmin: text.poleAdmin,
        poleImg: text.poleImg
    }
});

